/* -----------------------------------------*/
/* ラベル：トグルボタン */
/* -----------------------------------------*/
.toggle {
  display: inline-block;
  position: relative;
  width: 50px;
  height: 25px;
  border-radius: 50px;
  background-color: var(--color-dddddd); /*#dddddd;*/
  cursor: pointer;
  -webkit-transition: background-color .4s;
  transition: background-color .4s;
  margin-top: 5px;
}
.toggle:has(:checked) {
  background-color: var(--color-3464ff); /*#3464ff;*/
}
.toggle::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  -webkit-box-shadow: 0 0 5px var(--color-0010);
          box-shadow: 0 0 5px var(--color-0010); /*rgb(0 0 0 / 20%);*/
  background-color: var(--color-ffffff); /*#ffffff;*/
  content: '';
  -webkit-transition: left .4s;
  transition: left .4s;
}
.toggle:has(:checked)::after {
  left: 25px;
}
.toggleInput {
  display: none;
}
.toggle-td {
  width: 80px;
}
.toggle-p {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-left: 60px;
  margin: 1px;
  white-space: nowrap;
}
