.accordionDiv {
  /* 展開部分 */
  max-width: 500px;
  margin-bottom: 7px;
  background-color: var(--color-ffffff); /*#ffffff;*/
  border-bottom: 2px solid var(--color-cccccc); /*#cccccc;*/
  border-radius: 0px;
  /* 枠の丸み */
}
.accordionDiv summary {
  /* タイトル部分 */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  padding: .1em .8em .2em .3em;
  color: #333333;
  cursor: pointer;
}
.accordionDiv summary::-webkit-details-marker {
  display: none;
}
.accordionDiv summary::after {
  /* 上下マーク部分 */
  -webkit-transform: translateY(-25%) rotate(45deg);
          transform: translateY(-25%) rotate(45deg);
  width: 7px;
  height: 7px;
  margin-left: 55px;
  border-bottom: 3px solid var(--color-b6bdc3); /*#b6bdc3;*/
  border-right: 3px solid var(--color-b6bdc3); /*#b6bdc3;*/
  content: '';
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}
.accordionDiv[open] summary::after {
  -webkit-transform: rotate(225deg);
          transform: rotate(225deg);
  margin-left: 50px;
}
.accordionDiv p {
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  opacity: 0;
  margin: 0;
  padding: .3em 2em 1.5em;
  color: #333333;
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  transition: transform .5s, opacity .5s;
  transition: transform .5s, opacity .5s, -webkit-transform .5s;
}
.accordionDiv[open] p {
  -webkit-transform: none;
          transform: none;
  opacity: 1;
}
